<template lang="pug">
BodyComponent
  #main
    .row
      C-Breadcrump(
        title="لیست تلنت‌ها"
        start="خانه"
        :startPath="{ name: 'home-page' }"
        middle="تلنت‌ها"
        :middlePath="{ name: 'all-active-talents' }"
        last="لیست تلنت‌ها"
      )
      .col.s12
        .container
          // users list start
          section.users-list-wrapper.section
            .users-list-table
              .card
                .card-content
                  // datatable start
                  C-Table(
                    :AllData="talents"
                    :headers=[ "شناسه", "نام", "موبایل", "محبوبیت", "بلاک", "فعال", "تأیید", ]
                    editComponent="edit-talent"
                    showComponent="show-talent"
                  )
                  // datatable ends
          // users list ends
</template>

<script>
import VueCookies from "vue-cookies";
import { AllTalentsScripts } from "@/common/global/scripts";
import { addStaticScripts } from "@/common/utils/addStatics";
import TalentService from "@/services/TalentService.js";
import BodyComponent from "@/layouts/BodyComponent";
export default {
  components: {
    BodyComponent,
  },
  data() {
    return {
      talents: {},
      token: "",
    };
  },
  created() {
    this.token = VueCookies.get("adminToken");
    TalentService.getAllTalentsByStatus(this.token, { isBlocked: false, isActive: true, isApproved: true })
      .then(response => (this.talents = response.data))
      .catch(error => console.log(`there was an error: ${error}`));
  },
  mounted() {
    addStaticScripts(AllTalentsScripts);
  },
};
</script>

<style lang="scss" scoped>
@import "/app-assets/vendors/data-tables/css/jquery.dataTables.min.css";
@import "/app-assets/vendors/data-tables/extensions/responsive/css/responsive.dataTables.min.css";
@import "/app-assets/css-rtl/pages/page-users.min.css";
</style>
