<template lang="pug">
.responsive-table
  #users-list-datatable_wrapper.dataTables_wrapper.no-footer
    table#users-list-datatable.table.dataTable.no-footer.dtr-inline(
      role="grid",
      aria-describedby="users-list-datatable_info",
      style="width: 1560px;"
    )
      thead
        tr(role="row")
          th.sorting_asc(rowspan="1", colspan="1", style="width: 32px;", aria-label="")
          th(
            v-for="(header, headerKey) in headers",
            :key="headerKey",
            tabindex="0",
            aria-controls="users-list-datatable",
            rowspan="1",
            colspan="1",
            style="width: 112px;",
            aria-label=`${header}: activate to sort column ascending`
          ) {{ header }}
          th.sorting_disabled(
            v-if="editComponent",
            rowspan="1",
            colspan="1",
            style="width: 129px;",
            aria-label="ویرایش"
          ) ویرایش
          th.sorting_disabled(v-if="showComponent", rowspan="1", colspan="1", style="width: 72px;", aria-label="نمایش") نمایش
      tbody
        tr(role="row", v-for="(data, index) in AllData.items", :class="[index % 2 ? 'even' : 'odd']", :key="index")
          td.sorting_1(tabindex="0")
          td {{ data.id }}
          td {{ data.name }}
          td {{ data.mobile }}
          td {{ data.popularity || 'خالی' }}

          td(v-if="data.isBlocked")
            span.chip.red.lighten-5
              span.red-text بلاک شده
          td(v-else)
            span.chip.green.lighten-5
              span.green-text فعال

          td(v-if="data.isActive")
            span.chip.green.lighten-5
              span.green-text فعال
          td(v-else)
            span.chip.red.lighten-5
              span.red-text غیرفعال

          td(v-if="data.isApproved")
            span.chip.green.lighten-5
              span.green-text تایید شده
          td(v-else)
            span.chip.red.lighten-5
              span.red-text تایید نشده
          td(v-if="editComponent")
            router-link(:to="{ name: editComponent, params: { id: data.id } }")
              i.material-icons edit
          td(v-if="showComponent")
            router-link(:to="{ name: showComponent, params: { id: data.id } }")
              i.material-icons remove_red_eye

    #users-list-datatable_paginate.dataTables_paginate.paging_simple_numbers
      a#users-list-datatable_previous.paginate_button.previous.disabled(
        aria-controls="users-list-datatable",
        data-dt-idx="0",
        tabindex="-1"
      ) اولین
      span
        //- a.paginate_button(:class="{ current: (currentPage - 1 === index) }" v-for="(page, index) in talents.meta.totalPages" :key="index" aria-controls="users-list-datatable" data-dt-idx="1" tabindex="0") {{ index + 1 }}
        a.paginate_button(aria-controls="users-list-datatable", data-dt-idx="1", tabindex="0") 1
      a#users-list-datatable_next.paginate_button.next(
        aria-controls="users-list-datatable",
        data-dt-idx="5",
        tabindex="0"
      ) آخرین
</template>

<script>
export default {
  props: {
    AllData: { type: Array },
    editComponent: { type: String },
    showComponent: { type: String },
    headers: { type: Array },
  },
};
</script>
